






import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({
  filters: {
    pluralize(length?: number): string {
      const word = length === 1 ? 'item' : 'items';
      return `${length} ${word} left`;
    },
  },
})
export default class TodoCount extends Vue {
  @Prop()
  private list!: number;
}
